import React, { useState } from 'react';
import { Select, Button, Popconfirm } from 'antd';

interface Props {
	onshelvesOpe: (name: string) => void
	disabled: boolean
}

const A: React.FC<Props> = (props) => {
	const [opeName, setOpeName] = useState();
	const cancel = () => {
	}
	return (
		<div style={{ position: 'absolute', bottom: 40, left: 20, }}>
		
			<Select placeholder='' style={{ width: 150 }} defaultValue={opeName}
				onChange={(value: string) => { setOpeName(value) }} disabled={props.disabled}>
				<Select.Option value='up'></Select.Option>
				<Select.Option value='down'></Select.Option>
			</Select>&nbsp;
			<Popconfirm
				title=''
				onConfirm={(e) => { props.onshelvesOpe(opeName) }}
				onCancel={cancel}
				okText=''
				cancelText=''>
				<Button type='primary' disabled={props.disabled}></Button>
			</Popconfirm>
		</div >
	)
}

export default A;