import React from 'react';
import Form, { FormComponentProps } from 'antd/lib/form';
import { Button, Input, Switch, Select, message } from 'antd';
import request from '../../../../request';

const { Option } = Select;

interface Props extends FormComponentProps<{
	flagEnable: boolean
	mobile: string
	nickName: string
	roleId: string
	userName: string
}> {
	beginLoading: () => void
	endLoading: () => void
	roles: { name: string, id: string }[]
}

const A: React.FC<Props> = (props) => {

	/**
	 * 
	 */
	const handleSubmit = () => {

		props.form.validateFieldsAndScroll((error, values) => {

			if (!error) {
				props.beginLoading()

				request.admin.user.saveUser(values).then((res: any) => {
					setTimeout(() => {
						props.endLoading()
					}, 300);

					if (res.code === 0) {
						message.success('')
					} else {
						message.error(`! (${ res.message || res.code })`)
					}
				})
			}
		})
	}

	/**
	 * 
	 */
	const itemID = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('userName', {
			rules: [{ required: true, message: '' }]
		})(<Input placeholder=' (ID)'></Input>)
	}
	/**
	 * 
	 */
	const itemName = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('nickName', {
			rules: [{ required: true, message: '' }]
		})(<Input placeholder=''></Input>)
	}

	/**
	 * 
	 */
	const itemPhone = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('mobile', {
			rules: [{ required: true, message: '' }]
		})(<Input placeholder=' ()'></Input>)
	}

	/**
	 * 
	 */
	const itemSale = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('roleId', {
			rules: [{ required: true, message: '' }]
		})(
			<Select placeholder=''>
				{
					props.roles.map(v => <Option key={v.id} value={v.id}>{v.name}</Option>)
				}
			</Select>
		)
	}

	/**
	 * 
	 */
	const itemEnable = () => {
		const { getFieldDecorator } = props.form;

		return getFieldDecorator('flagEnable', {
			initialValue: true, valuePropName: 'checked'
		})(<Switch checkedChildren='' unCheckedChildren='' />)
	}

	return (
		<div>
			<Form labelCol={{ span: 6 }} wrapperCol={{ span: 12 }}>
				<Form.Item label='' >
					{itemID()}
				</Form.Item>
				<Form.Item label='' >
					{itemName()}
				</Form.Item>
				<Form.Item label=''>
					{itemPhone()}
				</Form.Item>
				<Form.Item label='' hasFeedback>
					{itemSale()}
				</Form.Item>

				<Form.Item label=''>
					{itemEnable()}
				</Form.Item>
				<Form.Item label=''>
					<Button type='primary' style={{ minWidth: '100%' }} onClick={handleSubmit}></Button>
				</Form.Item>

			</Form>
		</div>
	)
}

export default Form.create<Props>()(A);